.App {
    text-align: center;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #282c34;
    color: white;
    padding: 0 20px;
    height: 4.2rem;
}

.right-nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.netblix-logo {
    width: 100%;
    height: 100%;
    object-fit: scale-down;
}

.netblix-logo:hover {
    cursor: pointer;
}

nav ul {
    display: flex;
    list-style: none;
}